
border-1px() {
  background: #f00;
}
.carousel-wrapper {
  width: 100%;
  margin: 0px auto 0 auto;
  padding: 0 10px 0 10px;
}
.el-carousel__item > span {
  font-size: 14px;
  opacity: 2;
  line-height: 200px;
  background: transparent;
  margin: 0;
  text-align: center;
}
.el-carousel__item > span > img {
  width: 200px;
}
#carousel-imgwrapper {
  text-align: center;
}
.el-carousel__item > img {
  width: 180px;
  height: 100px;
}


