
@import '../../common/lyicon/css/lyicon.css';
@import '../../../node_modules/animate.css';
#Mfooter {
  height: 60px;
  line-height: 60px;
  width: 100%;
  background: #58b7ff;
  z-index: 100;
}
#Mfooter > .portrait-wrapper {
  float: left;
  margin-left: 40px;
  background: #ccc;
  width: 60px;
  height: 60px;
  border-radius: 100px;
}
.footer-song-name {
  display: inline-block;
  width: 20%;
  overflow: hidden;
  margin-left: 5px;
  color: #ff0;
}
#Mfooter > .play-wrapper {
  height: 60px;
  float: right;
  margin-right: 10px;
  line-height: 60px;
}
.play-wrapper > .fa-pause-circle,
.play-wrapper > .fa-play-circle-o {
  line-height: 60px;
}
.my-icon-play {
  font-size: 30px;
  color: #fff;
  margin-left: 5px;
}
.footer-circle-img {
  width: 60px;
  height: 60px;
  border-radius: 30px;
}


