
@import '../node_modules/animate.css';
border-1px() {
  background: #f00;
}
body,
html {
  height: 100%;
  border-left: 1px #ccc solid;
  border-right: 1px #ccc solid;
}
#app {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  margin: 0 auto;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  overflow: hidden;
}
.menu-left {
  position: absolute;
  left: 0px;
  top: 0px;
}
#cssdog {
  position: absolute;
  top: 200px;
  left: 50%;
  margin-left: -75px;
}
.app-router-view {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  overflow: hidden;
}
.app-footer {
  height: 60px;
}
#app-header {
  height: 50px;
}
.slide-left-enter {
  opacity: 1;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.slide-left-leave {
  opacity: 1;
}
.slide-left-enter-active {
  opacity: 1;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  position: absolute;
  top: 0px;
}
.slide-left-leave-active {
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
  opacity: 1;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  position: absolute;
  top: 0px;
}
.slide-right-enter {
  opacity: 1;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
  top: 0px;
}
.slide-right-leave {
  opacity: 1;
}
.slide-right-enter-active {
  position: absolute;
  top: 0px;
  opacity: 1;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}
.slide-right-leave-active {
  position: absolute;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
  top: 0px;
  opacity: 1;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}


