@font-face {
  font-family: 'fontello';
  src: url('../font/fontello.eot?63403388');
  src: url('../font/fontello.eot?63403388#iefix') format('embedded-opentype'),
       url('../font/fontello.woff2?63403388') format('woff2'),
       url('../font/fontello.woff?63403388') format('woff'),
       url('../font/fontello.ttf?63403388') format('truetype'),
       url('../font/fontello.svg?63403388#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
