
@import url("../../common/stylus/mixin.styl");
#start-header {
  display: inline-block;
  width: 100%;
  background: #20a0ff;
  color: #e5e9f2;
  font-size: 36px;
  border-radius: 3px;
  margin: 0;
}
.app-header-menu {
  display: inline-block;
  color: #f9fafc;
  margin-left: 5px;
}
.font-wrapper {
  display: inline-block;
  margin-left: 30px;
}
.music-recommoned.router-link-active,
.music-ranking-list.router-link-active,
.music-personal.router-link-active {
  color: #fff;
  font-size: 23px;
  margin-top: 5px;
}
.music-recommoned,
.music-ranking-list,
.music-personal {
  display: inline-block;
  font-size: 21px;
  vertical-align: text-top;
  margin-top: 8px;
}
.search-wrapper {
  float: right;
  height: 50px;
  margin-right: 5px;
}
.start-header > .search-wrapper > .el-icon-search {
  display: inline-block;
  color: #f9fafc;
  line-height: 50px;
}


